import { Block } from '@ali/4ever-cangjie';
import { JsonML } from '@ali/4ever-utils';
import InlinePlaceholder from '../models';

export default {
  match(node: Block): boolean {
    return InlinePlaceholder.isInlinePlaceholder(node);
  },

  convert(node: Block): JsonML {
    return ['iph', node.data];
  },
};
